// 
import * as d3 from "d3";
import modeDataUrl from "./assets/mode_all.json";

// 
let nodes = [];
let links = [];
let simulation;
let selectedNode = null;
let currentYear = 2000; // 
let minYear = -1000;
let maxYear = 2000;
let updateFunction = null;
let nodeElements = null;
let linkElements = null;
let nodeContainer = null;
let linkContainer = null;
let g = null; // SVG
let projection = null; // 

// 
let timeColorScale = null;

// 
const regionCoordinates = {
  'Greece': [23.7275, 37.9838],
  'Europe': [10.0, 50.0],
  'USA': [-95.7129, 37.0902],
  'Middle East': [43.0, 25.0],
  'Iran': [53.6880, 32.4279],
  'North India': [77.2090, 28.6139],
  'South India': [77.5946, 12.9716],
  'China': [104.1954, 35.8617],
  'Japan': [138.2529, 36.2048],
  'Indonesia': [113.9213, -0.7893],
  'Sub-Saharan Africa': [20.0, 0.0],
  'Africa': [20.0, 0.0]
};

// 
async function initForceDirectedGraph() {
  try {
    //  - import
    let data;
    if (modeDataUrl && typeof modeDataUrl === 'object') {
      data = modeDataUrl;
    } else if (typeof modeDataUrl === 'string') {
      data = await d3.json(modeDataUrl);
    } else {
      // 
      try {
        data = await d3.json('./assets/mode_all.json');
      } catch (e) {
        console.error(':', e);
        throw e;
      }
    }
    console.log(':', data);
    
    // 
    const { nodes: graphNodes, links: graphLinks, yearRange } = transformData(data);
    console.log(':', graphNodes.length);
    console.log(':', graphLinks.length);
    console.log(':', yearRange);
    
    nodes = graphNodes;
    links = graphLinks;
    minYear = yearRange.min;
    maxYear = yearRange.max;
    currentYear = maxYear;
    
    // 
    timeColorScale = d3.scaleSequential(d3.interpolateViridis)
      .domain([minYear, maxYear]);

    // SVG
    const width = 1200;
    const height = 800;
    const svg = d3.select('#force-directed-chart');
    
    if (svg.empty()) {
      console.error(' #force-directed-chart ');
      return;
    }
    
    svg
      .attr('width', width)
      .attr('height', height);
    
    // 
    svg.selectAll('*').remove();
    
  // 
  g = svg.append('g');
  
  // 
  const mapContainer = g.append('g').attr('class', 'map-container');
  
  //  - 1200x800
  // scale
  projection = d3.geoNaturalEarth1()
    .scale(Math.min(width, height) / 2.8)  // 
    .translate([width / 2, height / 2]);
  
  // 
  console.log(':', {
    scale: projection.scale(),
    translate: projection.translate(),
    width,
    height
  });
  
  // 
  const testCoords = [
    { name: 'Europe', coords: [10.0, 50.0] },
    { name: 'China', coords: [104.1954, 35.8617] },
    { name: 'USA', coords: [-95.7129, 37.0902] }
  ];
  testCoords.forEach(test => {
    const [x, y] = projection(test.coords);
    console.log(`${test.name} : (${x?.toFixed(2)}, ${y?.toFixed(2)})`);
  });
  
  const path = d3.geoPath().projection(projection);
  
  // 
  window.forceProjection = projection;
  
  // 
  try {
    const worldMap = await d3.json('https://cdn.jsdelivr.net/npm/world-atlas@2/countries-110m.json');
    if (worldMap && worldMap.objects && worldMap.objects.countries) {
      //  topojson
      if (typeof topojson !== 'undefined') {
        const countries = topojson.feature(worldMap, worldMap.objects.countries);
        mapContainer.selectAll('path.country')
          .data(countries.features)
          .enter()
          .append('path')
          .attr('class', 'country')
          .attr('d', path)
          .attr('fill', '#f0f0f0')
          .attr('stroke', '#ddd')
          .attr('stroke-width', 0.5)
          .style('opacity', 0.6);
      } else {
        // 
        mapContainer.append('rect')
          .attr('width', width)
          .attr('height', height)
          .attr('fill', '#f5f5f5')
          .attr('stroke', '#ddd')
          .attr('stroke-width', 1);
      }
    }
  } catch (e) {
    console.log('');
    // 
    mapContainer.append('rect')
      .attr('width', width)
      .attr('height', height)
      .attr('fill', '#f5f5f5')
      .attr('stroke', '#ddd')
      .attr('stroke-width', 1);
  }
  
  // 
  const zoom = d3.zoom()
    .scaleExtent([0.1, 4])
    .on('zoom', (event) => {
      g.attr('transform', event.transform);
    });
  svg.call(zoom);
  
  // 
  const defs = svg.append('defs');
  const marker = defs.append('marker')
    .attr('id', 'arrowhead')
    .attr('viewBox', '0 -5 10 10')
    .attr('refX', 15)
    .attr('refY', 0)
    .attr('markerWidth', 6)
    .attr('markerHeight', 6)
    .attr('orient', 'auto');
  marker.append('path')
    .attr('d', 'M0,-5L10,0L0,5')
    .attr('fill', '#999');
  
  // tooltip
  const tooltip = d3.select('body').append('div')
    .attr('class', 'force-tooltip')
    .style('opacity', 0)
    .style('position', 'absolute')
    .style('background', 'rgba(0, 0, 0, 0.9)')
    .style('color', 'white')
    .style('padding', '12px 16px')
    .style('border-radius', '8px')
    .style('font-size', '13px')
    .style('pointer-events', 'none')
    .style('z-index', 1000)
    .style('max-width', '300px');
  
  // 
  simulation = d3.forceSimulation(nodes)
    .force('link', d3.forceLink(links)
      .id(d => d.id)
      .distance(d => 100 / (d.strength || 1))
      .strength(d => d.strength || 0.5))
    .force('charge', d3.forceManyBody().strength(-300))
    .force('center', d3.forceCenter(width / 2, height / 2))
    .force('collision', d3.forceCollide().radius(d => d.radius + 5))
    .force('geoPosition', function(alpha) {
      // 
      nodes.forEach(node => {
        if (node.geoCoordinates && projection) {
          const [lon, lat] = node.geoCoordinates;
          const [targetX, targetY] = projection([lon, lat]);
          
          if (targetX !== null && targetY !== null && !isNaN(targetX) && !isNaN(targetY)) {
            const k = alpha * 0.15; // 
            node.vx += (targetX - node.x) * k;
            node.vy += (targetY - node.y) * k;
          }
        }
      });
    })
    .force('rootConstraint', function(alpha) {
      // 
      const centerX = width / 2;
      const centerY = height / 2;
      const maxRadius = Math.min(width, height) * 0.3; // 30%
      
      nodes.forEach(node => {
        // depth === 0 
        if (node.depth === 0 || !node.parent) {
          const dx = node.x - centerX;
          const dy = node.y - centerY;
          const distance = Math.sqrt(dx * dx + dy * dy);
          
          if (distance > maxRadius) {
            // 
            const angle = Math.atan2(dy, dx);
            const targetX = centerX + Math.cos(angle) * maxRadius;
            const targetY = centerY + Math.sin(angle) * maxRadius;
            const k = alpha * 0.5; // 
            node.vx += (targetX - node.x) * k;
            node.vy += (targetY - node.y) * k;
          }
        }
      });
    });
  
  // 
  linkContainer = g.append('g').attr('class', 'links');
  linkElements = linkContainer.selectAll('line').data([]);
  
  // 
  nodeContainer = g.append('g').attr('class', 'nodes');
  nodeElements = nodeContainer.selectAll('g').data([]);
  
  // 
  const yearSlider = d3.select('#year-slider');
  const yearDisplay = d3.select('#year-display');
  
  yearSlider
    .attr('min', minYear)
    .attr('max', maxYear)
    .attr('value', currentYear)
    .on('input', function() {
      currentYear = +this.value;
      yearDisplay.text(currentYear);
      if (updateFunction) {
        updateFunction();
      }
    });
  
  yearDisplay.text(currentYear);
  
  // 
  function update() {
    // 
    const visibleNodes = nodes.filter(n => {
      if (n.year_start === undefined || n.year_start === null) return true;
      return n.year_start <= currentYear;
    });
    
    const visibleLinks = links.filter(l => {
      const sourceNode = nodes.find(n => n.id === (l.source.id || l.source));
      const targetNode = nodes.find(n => n.id === (l.target.id || l.target));
      if (!sourceNode || !targetNode) return false;
      if (l.year !== undefined && l.year !== null && l.year > currentYear) return false;
      return visibleNodes.includes(sourceNode) && visibleNodes.includes(targetNode);
    });
    
    // 
    if (!nodeContainer) return; // 
    nodeElements = nodeContainer.selectAll('g.node')
      .data(visibleNodes, d => d.id);
    
    const nodeEnter = nodeElements.enter()
      .append('g')
      .attr('class', 'node')
      .style('opacity', 0)
      .call(drag(simulation));
    
    nodeEnter.append('circle')
      .attr('r', d => d.radius)
      .attr('fill', d => {
        // colorYear
        if (d.colorYear !== undefined && d.colorYear !== null && timeColorScale) {
          return timeColorScale(d.colorYear);
        }
        return '#999'; // 
      })
      .attr('stroke', '#fff')
      .attr('stroke-width', 2)
      .style('cursor', 'pointer');
    
    nodeEnter.append('text')
      .text(d => d.name)
      .attr('dx', d => d.radius + 5)
      .attr('dy', 4)
      .attr('font-size', '12px')
      .attr('fill', '#333')
      .style('pointer-events', 'none');
    
    nodeEnter
      .on('click', function(event, d) {
        event.stopPropagation();
        if (selectedNode === d) {
          d.fx = null;
          d.fy = null;
          selectedNode = null;
          highlightNeighbors(null);
        } else {
          selectedNode = d;
          d.fx = d.x;
          d.fy = d.y;
          highlightNeighbors(d);
        }
        simulation.alpha(0.3).restart();
      })
      .on('mouseover', function(event, d) {
        showTooltip(event, d, tooltip);
      })
      .on('mousemove', function(event) {
        tooltip
          .style('left', (event.pageX + 10) + 'px')
          .style('top', (event.pageY - 10) + 'px');
      })
      .on('mouseout', function() {
        hideTooltip(tooltip);
      });
    
    nodeEnter.merge(nodeElements)
      .transition()
      .duration(500)
      .style('opacity', 1);
    
    nodeElements.exit()
      .transition()
      .duration(500)
      .style('opacity', 0)
      .remove();
    
    // 
    if (!linkContainer) return; // 
    linkElements = linkContainer.selectAll('line.link')
      .data(visibleLinks, d => `${d.source.id}-${d.target.id}`);
    
    const linkEnter = linkElements.enter()
      .append('line')
      .attr('class', 'link')
      .attr('stroke', d => {
        const sourceNode = nodes.find(n => n.id === d.source.id);
        // colorYear
        if (sourceNode && sourceNode.colorYear !== undefined && sourceNode.colorYear !== null && timeColorScale) {
          return timeColorScale(sourceNode.colorYear);
        }
        return '#999';
      })
      .attr('stroke-opacity', 0)
      .attr('stroke-width', d => Math.sqrt(d.strength || 0.5) * 2)
      .attr('marker-end', 'url(#arrowhead)');
    
    linkEnter.merge(linkElements)
      .transition()
      .duration(500)
      .attr('stroke-opacity', 0.6);
    
    linkElements.exit()
      .transition()
      .duration(500)
      .attr('stroke-opacity', 0)
      .remove();
    
    // 
    simulation.nodes(visibleNodes);
    simulation.force('link').links(visibleLinks);
    
    // 
    visibleNodes.forEach(node => {
      if (node.x === undefined || node.y === undefined) {
        if (node.geoCoordinates && projection) {
          const [lon, lat] = node.geoCoordinates;
          const projected = projection([lon, lat]);
          
          if (projected && projected.length === 2) {
            const [x, y] = projected;
            // 
            if (x !== null && y !== null && !isNaN(x) && !isNaN(y)) {
              node.x = x;
              node.y = y;
              console.log(` ${node.name} : (${x.toFixed(2)}, ${y.toFixed(2)})`);
            } else {
              node.x = width / 2 + (Math.random() - 0.5) * 100;
              node.y = height / 2 + (Math.random() - 0.5) * 100;
            }
          } else {
            node.x = width / 2 + (Math.random() - 0.5) * 100;
            node.y = height / 2 + (Math.random() - 0.5) * 100;
          }
        } else {
          node.x = width / 2 + (Math.random() - 0.5) * 100;
          node.y = height / 2 + (Math.random() - 0.5) * 100;
        }
      }
    });
    
    simulation.alpha(1).restart();
  }
  
  // 
  function highlightNeighbors(centerNode) {
    if (!centerNode) {
      // 
      nodeElements.style('opacity', 1);
      linkElements.style('opacity', 0.6);
      return;
    }
    
    // 
    const neighborIds = new Set();
    neighborIds.add(centerNode.id);
    
    links.forEach(l => {
      const sourceId = l.source.id || l.source;
      const targetId = l.target.id || l.target;
      if (sourceId === centerNode.id) {
        neighborIds.add(targetId);
      }
      if (targetId === centerNode.id) {
        neighborIds.add(sourceId);
      }
    });
    
    // 
    nodeElements.style('opacity', d => neighborIds.has(d.id) ? 1 : 0.1);
    linkElements.style('opacity', l => {
      const sourceId = l.source.id;
      const targetId = l.target.id;
      return neighborIds.has(sourceId) && neighborIds.has(targetId) ? 0.6 : 0.1;
    });
  }
  
  // 
  function drag(simulation) {
    function dragstarted(event, d) {
      if (!event.active) simulation.alphaTarget(0.3).restart();
      d.fx = d.x;
      d.fy = d.y;
    }
    
    function dragged(event, d) {
      d.fx = event.x;
      d.fy = event.y;
    }
    
    function dragended(event, d) {
      if (!event.active) simulation.alphaTarget(0);
      if (!selectedNode || selectedNode !== d) {
        d.fx = null;
        d.fy = null;
      }
    }
    
    return d3.drag()
      .on('start', dragstarted)
      .on('drag', dragged)
      .on('end', dragended);
  }
  
  // tooltip
  function showTooltip(event, d, tooltip) {
    tooltip
      .html(`
        <div class="tooltip-title">${d.name}</div>
        <div class="tooltip-content">
          <div class="tooltip-item">
            <span class="tooltip-label">ID:</span>
            <span class="tooltip-value">${d.id}</span>
          </div>
          ${d.year_start !== undefined && d.year_start !== null ? `
          <div class="tooltip-item">
            <span class="tooltip-label">:</span>
            <span class="tooltip-value">${d.year_start}</span>
          </div>
          ` : ''}
          ${d.region_origin ? `
          <div class="tooltip-item">
            <span class="tooltip-label">:</span>
            <span class="tooltip-value">${d.region_origin}</span>
          </div>
          ` : ''}
          ${d.category ? `
          <div class="tooltip-item">
            <span class="tooltip-label">:</span>
            <span class="tooltip-value">${d.category}</span>
          </div>
          ` : ''}
          ${d.scale_degrees ? `
          <div class="tooltip-item">
            <span class="tooltip-label">:</span>
            <span class="tooltip-value">${d.scale_degrees}</span>
          </div>
          ` : ''}
          ${d.note ? `
          <div class="tooltip-item">
            <span class="tooltip-label">:</span>
            <span class="tooltip-value">${d.note}</span>
          </div>
          ` : ''}
        </div>
      `)
      .style('left', (event.pageX + 10) + 'px')
      .style('top', (event.pageY - 10) + 'px')
      .transition()
      .duration(200)
      .style('opacity', 1);
  }
  
  // tooltip
  function hideTooltip(tooltip) {
    tooltip.transition()
      .duration(200)
      .style('opacity', 0);
  }
  
  // tick - 
  simulation.on('tick', () => {
    // 
    if (linkContainer) {
      linkContainer.selectAll('line.link')
        .attr('x1', d => d.source.x)
        .attr('y1', d => d.source.y)
        .attr('x2', d => d.target.x)
        .attr('y2', d => d.target.y);
    }
    
    // 
    if (nodeContainer) {
      nodeContainer.selectAll('g.node')
        .attr('transform', d => {
          if (d.x === undefined || d.y === undefined) {
            // 
            d.x = width / 2 + (Math.random() - 0.5) * 100;
            d.y = height / 2 + (Math.random() - 0.5) * 100;
          }
          return `translate(${d.x},${d.y})`;
        });
    }
  });
  
  // 
  createLegend(nodes, minYear, maxYear);
  
  // 
  createLegend(nodes, minYear, maxYear);
  
  // 
  updateFunction = update;
  
  // 
  createLegend(nodes, minYear, maxYear);
  
  // 
  update();

  // 
  return { update, highlightNeighbors };
  } catch (error) {
    console.error(':', error);
    throw error;
  }
}

// 
function createLegend(nodes, minYear, maxYear) {
  const legendSvg = d3.select('#force-legend-svg');
  
  // 
  legendSvg.selectAll('*').remove();
  
  // SVG
  const displayWidth = 1200;  // 3400 * 3
  const displayHeight = 90;  // height45 * 2
  // viewBoxdisplay
  const aspectRatio = displayWidth / displayHeight;  // 13.33:1
  const viewBoxWidth = 800;
  const viewBoxHeight = viewBoxWidth / aspectRatio;  // 60
  
  legendSvg
    .attr('width', displayWidth)
    .attr('height', displayHeight)
    .attr('viewBox', `0 0 ${viewBoxWidth} ${viewBoxHeight}`)
    .attr('preserveAspectRatio', 'none');
  
  // viewBox
  const legendG = legendSvg.append('g').attr('transform', 'translate(10, 15)');
  
  // 1. - 
  const colorLegendX = 0;
  const colorLegendY = 0;
  const colorTextWidth = 120;  // 
  
  legendG.append('text')
    .attr('x', colorLegendX)
    .attr('y', colorLegendY + 15)
    .attr('font-size', '12px')
    .attr('font-weight', 'bold')
    .attr('fill', '#333')
    .text(': ');
  
  const colorBarWidth = 200;
  const colorBarHeight = 20;
  const colorBarX = colorLegendX + colorTextWidth + 10;  // 
  const colorBarY = colorLegendY;
  
  // 
  const defs = legendSvg.append('defs');
  const gradient = defs.append('linearGradient')
    .attr('id', 'time-gradient')
    .attr('x1', '0%')
    .attr('x2', '100%');
  
  // 
  const steps = 10;
  for (let i = 0; i <= steps; i++) {
    const t = i / steps;
    const year = minYear + (maxYear - minYear) * t;
    gradient.append('stop')
      .attr('offset', `${t * 100}%`)
      .attr('stop-color', timeColorScale(year));
  }
  
  // 
  legendG.append('rect')
    .attr('x', colorBarX)
    .attr('y', colorBarY)
    .attr('width', colorBarWidth)
    .attr('height', colorBarHeight)
    .attr('fill', 'url(#time-gradient)')
    .attr('stroke', '#ccc')
    .attr('stroke-width', 1);
  
  // 
  legendG.append('text')
    .attr('x', colorBarX)
    .attr('y', colorBarY + colorBarHeight + 15)
    .attr('font-size', '10px')
    .attr('fill', '#666')
    .text(minYear);
  
  legendG.append('text')
    .attr('x', colorBarX + colorBarWidth)
    .attr('y', colorBarY + colorBarHeight + 15)
    .attr('font-size', '10px')
    .attr('fill', '#666')
    .attr('text-anchor', 'end')
    .text(maxYear);
  
  // 2. - 
  const sizeLegendX = 400;
  const sizeLegendY = 0;
  const sizeTextWidth = 120;  // 
  
  legendG.append('text')
    .attr('x', sizeLegendX)
    .attr('y', sizeLegendY + 15)
    .attr('font-size', '12px')
    .attr('font-weight', 'bold')
    .attr('fill', '#333')
    .text(': ');
  
  // 
  const radii = nodes.map(n => n.radius).filter(r => r !== undefined);
  if (radii.length === 0) return;
  
  const minRadius = Math.min(...radii);
  const maxRadius = Math.max(...radii);
  
  // 
  const sizeExamples = [
    { radius: minRadius, label: '' },
    { radius: (minRadius + maxRadius) / 2, label: '' },
    { radius: maxRadius, label: '' }
  ];
  
  const sizeStartX = sizeLegendX + sizeTextWidth + 10;  // 
  const sizeStartY = sizeLegendY;
  const sizeSpacing = 60;
  
  sizeExamples.forEach((example, i) => {
    const x = sizeStartX + i * sizeSpacing;
    const y = sizeStartY;
    
    // 
    legendG.append('circle')
      .attr('cx', x + 15)
      .attr('cy', y)
      .attr('r', example.radius * 0.6)  // 60%
      .attr('fill', '#4a90e2')
      .attr('stroke', '#fff')
      .attr('stroke-width', 1);
    
    // 
    legendG.append('text')
      .attr('x', x + 15)
      .attr('y', y + example.radius + 15)
      .attr('font-size', '11px')
      .attr('fill', '#666')
      .attr('text-anchor', 'middle')
      .text(example.label);
  });
}

// 
function transformData(data) {
  const nodeMap = new Map();
  const linkList = [];
  let nodeId = 0;
  let minYear = Infinity;
  let maxYear = -Infinity;
  
  // 
  function traverse(obj, parent = null, category = null, depth = 0) {
    const id = `node_${nodeId++}`;
    const name = obj.name || 'Unknown';
    const year_start = obj.year_start;
    const region_origin = obj.region_origin;
    const scale_degrees = obj.scale_degrees;
    const note = obj.note;
    
    if (year_start !== undefined) {
      minYear = Math.min(minYear, year_start);
      maxYear = Math.max(maxYear, year_start);
    }
    
    // 
    let nodeCategory = category || obj.name;
    if (depth === 0) {
      nodeCategory = 'Root';
    } else if (depth === 1) {
      nodeCategory = name;
    }
    
    //  region_origin 
    let geoCoordinates = null;
    if (region_origin && regionCoordinates[region_origin]) {
      geoCoordinates = regionCoordinates[region_origin];
    }
    
    // 
    let colorYear = null;
    if (parent) {
      // colorYear
      if (parent.colorYear !== null && parent.colorYear !== undefined) {
        colorYear = parent.colorYear;
      } else if (parent.year_start !== undefined && parent.year_start !== null) {
        // colorYearyear_start
        colorYear = parent.year_start;
      }
    }
    
    // year_start
    if ((colorYear === null || colorYear === undefined) && year_start !== undefined && year_start !== null) {
      colorYear = year_start;
    }
    
    // 
    const node = {
      id,
      name,
      year_start,
      colorYear, // 
      region_origin,
      geoCoordinates, // 
      scale_degrees, // 
      note, // 
      category: nodeCategory,
      size: obj.children ? obj.children.length : 1,
      radius: Math.sqrt(obj.children ? obj.children.length : 1) * 5 + 5,
      depth,
      parent: parent // 
    };
    
    nodeMap.set(id, node);
    
    // 
    if (parent) {
      linkList.push({
        source: parent.id,
        target: id,
        strength: 1 / (depth + 1),
        year: year_start || parent.year_start || 0
      });
    }
    
    // 
    if (obj.children && Array.isArray(obj.children)) {
      obj.children.forEach(child => {
        traverse(child, node, nodeCategory, depth + 1);
      });
    }
    
    return node;
  }
  
  // 
  if (data.children && Array.isArray(data.children)) {
    data.children.forEach(child => {
      traverse(child, null, null, 0);
    });
  } else {
    traverse(data, null, null, 0);
  }
  
  // ID
  const links = linkList.map(l => ({
    ...l,
    source: nodeMap.get(l.source),
    target: nodeMap.get(l.target)
  }));
  
  // colorYear
  const allNodes = Array.from(nodeMap.values());
  const defaultYear = minYear !== Infinity ? minYear : (maxYear !== -Infinity ? maxYear : 0);
  
  // colorYear
  allNodes.forEach(node => {
    if (node.colorYear === null || node.colorYear === undefined) {
      // colorYearyear_start
      let currentParent = node.parent;
      while (currentParent) {
        if (currentParent.colorYear !== null && currentParent.colorYear !== undefined) {
          node.colorYear = currentParent.colorYear;
          break;
        } else if (currentParent.year_start !== undefined && currentParent.year_start !== null) {
          node.colorYear = currentParent.year_start;
          break;
        }
        currentParent = currentParent.parent;
      }
      // 
      if (node.colorYear === null || node.colorYear === undefined) {
        node.colorYear = defaultYear;
      }
    }
  });
  
  return {
    nodes: allNodes,
    links,
    yearRange: {
      min: minYear === Infinity ? 0 : minYear,
      max: maxYear === -Infinity ? 2000 : maxYear
    }
  };
}

// 
export { initForceDirectedGraph, currentYear, minYear, maxYear };

// 
if (typeof window !== 'undefined') {
  initForceDirectedGraph().catch(console.error);
}

